import React, {useState, ChangeEvent} from 'react'
import {Button, Input, Typography, List, Progress, Tag, Layout, Select} from 'antd'
import logo from './logo.svg'
import './App.css';

const { Search, TextArea } = Input
const { Title, Paragraph, Text } = Typography
const { Header, Footer, Sider, Content } = Layout
const { Option} = Select

const apiCode = 'BHdesj3dRp0zu9ud9aaUPSs/QZ0ZewaUtjpZLqTMY2elfY5cKRICZQ=='

const proxyurl = "https://cors-anywhere.herokuapp.com/";
const domain = 'https://openapi.naver.com/v1/search/shop.json?display=100&start=1&sort=sim&query='
const okMallApi = 'http://52.231.194.182:8080/v1/okmall'

interface prodcutInfo {
  code: string,
  name: string,
  count: number,
  link: string
}

const App: React.FC = () => {

  const [okMallCategoryText, setOkMallCategoryText] = useState('ALL')
  const [okMallProductText, setOkMallProductText] = useState('')
  const [naverSearchProductCodes, setNaverSearchProductCodes] = useState('')

  const [okProductInfos, setokProductInfos] = useState<Array<prodcutInfo>>()
  const [naverProductInfos, setNaverProductInfos] = useState<Array<prodcutInfo>>()

  
  
  const [progress, setProgress] = useState<number>(0)
  

  let finds:Array<string> = new Array()
  let index = 0
  let findsCount = 0
  let naverProdcuts: Array<prodcutInfo> = new Array() 
  let okPoducts: Array<prodcutInfo> = new Array()
  
  const callFetch = async (text:string) => {
    if(text != "jyp"){
      fetch(proxyurl + domain + encodeURI(text), {
        headers: {'X-Naver-Client-Id':'mnUoJCdcr7xsozFA0gRJ', 'X-Naver-Client-Secret': 'zAkagcFUfG'}
      }).
      then(data => {
        if(data.status != 200){
          console.log("error: "+data.status.toString() + " | " + JSON.stringify(data))
        }
        return data.json()}).
      then(item => {
        const product:prodcutInfo = {
          code:text,
          name:"",
          count:item.total,
          link:""
        }
        naverProdcuts.push(product)
        index++
        setProgress(100*index/findsCount)
        if(index < findsCount) {
          callFetch(finds[index])
        } else {
          setNaverProductInfos(naverProdcuts)
        }
      })
    } else {
      const product:prodcutInfo = {
        code:"백지연 힘내라",
        name:"사랑의 불시착",
        count:0,
        link:""
      }
      naverProdcuts.push(product)
      index++
      setProgress(100*index/findsCount)
      if(index < findsCount) {
        callFetch(finds[index])
      } else {
        setNaverProductInfos(naverProdcuts)
      }
    }
    
  }

  const getFetch = async () => {
    findsCount = finds.length
    callFetch(finds[index]).then()

    // finds.map( code => {
      // const item = callFetch(finds[0])
      // item.then( item => prodcuts.push(item))
    // })
    // setItems(prodcuts)
  }

  const okMallProductNameInputChange = (e:ChangeEvent<HTMLInputElement>) => {
    setOkMallProductText(e.target.value)
  }

  const naverSearchProductCodeInputChange = (e:ChangeEvent<HTMLInputElement>) => {
    setNaverSearchProductCodes(e.target.value)
  } 

  const getOkMallFetch = async (text:string, category:string) => {

    if(text.length <= 0 && category ==='ALL'){
      alert('category를 선택하거나 검색어를 입력하세요')
      return
    } 
    setNaverSearchProductCodes('')
    try {
      if(category === 'ALL'){
         category = ''
      }
      const response = await fetch(proxyurl+okMallApi+"?keyword="+encodeURI(text)+'&category='+encodeURI(category)+'&code='+apiCode);
      const json = await response.json();
      finds = []
      json.map((item:prodcutInfo) => {
        const product:prodcutInfo = {
          code:(item.code.length > 0 ? item.code : "jyp"),
          name:(item.name.length > 0 ? item.name : "jyp"),
          count:0,
          link:item.link
        }
        okPoducts.push(product)
        finds.push(product.code)  
      })
      setokProductInfos(okPoducts)

      if(finds.length <= 0)  return
      index = 0
      setProgress(index)
      naverProdcuts.splice(0,naverProdcuts.length)
      getFetch()
    } catch (err) {
        return console.log(err)
    }
  }
  
  const okSearchBtnClick = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
      getOkMallFetch(okMallProductText, okMallCategoryText)
  }

  const naverSearchCodeBtnClick = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if(naverSearchProductCodes.length <= 0) {
      alert('검색할 코드를 입력하세요')
      return
    } 
    index = 0
    setProgress(index)
    naverProdcuts.splice(0,naverProdcuts.length)
    finds = naverSearchProductCodes.split(',')
    getFetch()
  }

  const onOkEnterKey = (e:React.KeyboardEvent<HTMLInputElement>) => {
    if(e.keyCode==13) {
      getOkMallFetch(okMallProductText, okMallCategoryText)
    }
  }

  const onNaverEnterKey = (e:React.KeyboardEvent<HTMLInputElement>) => {
    if(e.keyCode==13) {
      if(naverSearchProductCodes.length <= 0)  return
      index = 0
      setProgress(index)
      naverProdcuts.splice(0,naverProdcuts.length)
      finds = naverSearchProductCodes.split(',')
      getFetch()
    }
  }
//   return(
//     <div className="App">
//       <Layout>
//       <Header>Header</Header>
//       <Content>Content</Content>
//       <Footer>Footer</Footer>
//     </Layout>

//     </div>
//   )
// }
// export default App;

  const onCategoryChange = (category: string) => {
    setOkMallCategoryText(category)
  }

  return (
    <div className="App">
      <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet"></link>
      <header className="App-header">
        <img src='https://shop-phinf.pstatic.net/20190702_17/ncp_1nq2uj_01_1562054355047mnXo4_JPEG/5010845439258749_-647527624.jpg' className="App-logo" alt="logo" />
      </header>
        <div><Progress strokeLinecap="square" percent={progress} /></div>
        <div className="d-flex justify-content-between">
          <div className="p-2 flex-fill bd-highlight"> 
            <div className="d-flex justify-content-center">
              <div className="p-2 bd-highlight"> 
                <div className="p-3 mb-2 text-dark">Ok Mall</div>
              </div>
            </div>
            <div className="input-group mb-3">
              <Select defaultValue="ALL" style={{ width: 120 }} onChange={onCategoryChange}>
                <Option value="ALL">ALL</Option>
                <Option value="luxury">luxury</Option>
                <Option value="men">men</Option>
                <Option value="women">women</Option>
                <Option value="life">life</Option>
              </Select>
              <input type="text" 
                className="form-control" 
                id="okProductNameInput" 
                onKeyUp={onOkEnterKey}
                value={okMallProductText}
                onChange={okMallProductNameInputChange}
                placeholder="상품명" 
                aria-label="okmall prodcuct name" 
                aria-describedby="okSearchBtn"/>
              <div className="input-group-append">
                <button className="btn btn-outline-dark" type="button" id="okSearchBtn" onClick={okSearchBtnClick}>
                  Search
                </button>
              </div>
            </div>
            <div className="d-flex justify-content-center">
              <div className="p-2 bd-highlight"> 
                <List
                  header={<div>상품명</div>}
                  footer={<div></div>}
                  bordered
                  dataSource={okProductInfos}
                  renderItem={item => (
                    <List.Item>
                      {item.name}
                    </List.Item>
                  )}
                />
              </div>
              <div className="p-2 bd-highlight">
                <List
                  header={<div>코드</div>}
                  footer={<div></div>}
                  bordered
                  dataSource={okProductInfos}
                  renderItem={item => (
                    <List.Item>
                      {item.code}
                    </List.Item>
                  )}
                />
              </div>
              <div className="p-2 bd-highlight"> 
                <List
                  header={<div>링크</div>}
                  footer={<div></div>}
                  bordered
                  dataSource={okProductInfos}
                  renderItem={item => (
                    <List.Item>
                      <a href={item.link} target="_blank" className="text-primary">{item.link}</a>
                    </List.Item>
                  )}
                />
              </div>
            </div>
          </div>
          <div className="p-2 flex-fill bd-highlight">
            <div className="d-flex justify-content-center">
              <div className="p-2 bd-highlight"> 
                <div className="p-3 mb-2 text-dark">Naver Search</div>
              </div>
            </div>
            <div className="input-group mb-3">
              <input type="text" 
                className="form-control" 
                id="naverSearchProdcutCodeInput" 
                value={naverSearchProductCodes}
                onKeyUp={onNaverEnterKey}
                onChange={naverSearchProductCodeInputChange}
                placeholder="상품코드" 
                aria-label="naver serarch prodcuct code" 
                aria-describedby="naverSearchProdcutCodeBtn"/>
              <div className="input-group-append">
                <button className="btn btn-outline-dark" type="button" id="naverSearchProdcutCodeBtn" onClick={naverSearchCodeBtnClick}>
                  Search
                </button>
              </div>
            </div>
            <div className="d-flex justify-content-center">
              <div className="p-2 bd-highlight"> 
                <List
                  header={<div>상품코드</div>}
                  footer={<div></div>}
                  bordered
                  dataSource={naverProductInfos}
                  renderItem={item => (
                    <List.Item>
                      {item.code}
                    </List.Item>
                  )}
                />
              </div>
              <div className="p-2 bd-highlight"> 
                <List
                  header={<div>검색개수</div>}
                  footer={<div></div>}
                  bordered
                  dataSource={naverProductInfos}
                  renderItem={item => (
                    <List.Item>
                      {item.count}
                    </List.Item>
                  )}
                />
              </div>
            </div>
          </div>
        </div>
    </div>
  );
}



export default App;
